import React from "react";
import { Box, Typography } from "@mui/material";
import cover from "../../static/img/cover.jpg"
import { info } from "../../static/info"
import ListenLinks from "../UI/ListenLinks";


const Start = () => {
    document.title = `${info.track} - Слушать на всех площадках`
    return (
            <>
            <main>
                <div>
                    <Typography
                        variant="h6"
                        sx={{ ml: 0.5, mb: -1 }}
                        className="note2"
                    >
                        {info.track_type}&nbsp;&middot;&nbsp;{info.year_release}
                    </Typography>
                    <div className="flex-row wrap">
                        
                        <Typography
                            variant="h2"
                        >
                            {info.track}&nbsp;
                        </Typography>
                        <Typography 
                            variant="overline" 
                            sx={{ fontSize: 14 }}
                            className="prod"
                        >
                            {info.prod}
                        </Typography>
                    </div>
                    <div className="flex-row">
                        <Typography
                            variant="h4"
                            className="note"
                        >
                            {info.singer}&nbsp;
                        </Typography>
                        
                    </div>
                </div> 
                <Box 
                    sx={{ mt: 3 }}
                    className="cover-box"
                >
                    <img 
                        alt={`Обложка трека ${info.track}, исполнителя ${info.singer}`}
                        title={`Обложка трека "${info.track} - ${info.singer}"`}
                        src={cover}
                        className="cover"
                    />
                </Box>
            </main>
            <ListenLinks />
        </>
    )
}

export default Start;