import React from "react";
import { Box, Typography } from "@mui/material";

const FooterOnPage = () => {
    return (
        <footer>
            <Box
                sx={{ my: 3 }}
                className="flex-column listen-box"
            >
                <div className="flex-row note">
                    <Typography variant="subtitle1">
                        created by&nbsp;
                    </Typography>
                    <a href="https://github.com/roso1nik" target="_blank" rel="noopener noreferrer">
                        roso1nik
                    </a>
                </div>
                <Typography variant="subtitle2" className="note">
                    все права принадлежат их правообладателям
                </Typography>
            </Box> 
        </footer>
    )
}

export default FooterOnPage;